$bg: #22212c;
$dark: darken(
	$color: $bg,
	$amount: 2,
);
.kiwi {
	max-width: 60px;
	width: 100%;
	&:hover {
		animation: here .1s 4;
	}
}

.Home {
	display: flex;
	background-color: $dark;
	box-shadow: inset 0 -2px 2px darken($color: $bg, $amount: 6),
		inset 0 2px 2px darken($color: $bg, $amount: 6);
	justify-content: center;
	align-items: center;
	& > * {
		user-select: none;
		margin: 10px;
	}
	.resume {
		color: #66d9ef;
		cursor: pointer;
		background-color: darken($color: $bg, $amount: 6);
		padding: 10px;
		border-radius: 2px;
		transition: color 0.3s;
		&:hover {
			color: #50fa7b;
		}
	}
}

@keyframes here {
	25% {
		transform: rotate(2deg);
	}
	75% {
		transform: rotate(-2deg);
	}
}
