$bg: #22212c;
* {
	font-family: "JetBrainsMono";
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
.CodeMirror {
	height: auto;
	border-radius: 0;
}

.CodeMirror * {
	font-family: "JetBrainsMono";
	font-size: 1em;
}

.CodeMirror-vscrollbar {
	overflow: hidden;
}

.cm-s-dracula.CodeMirror,
.cm-s-dracula .CodeMirror-gutters {
	background-color: $bg !important;
	color: #f8f8f2 !important;
	border: none;
}