@font-face {
	font-family: "JetBrainsMono";
	font-style: normal;
	font-weight: 400;
	src: url("./fonts/JetBrainsMono-Regular.ttf"); /* IE9 Compat Modes */
	src: local("JetBrainsMono"), local("JetBrainsMono"),
		url("./fonts/JetBrainsMono-Regular.ttf");
}
$bg: #22212c;
.link_editor {
	color: #f1fa8c;
	font-size: 1.1em;
	text-shadow: 0 0 6px #44bd32;
	text-decoration: none;
}

body,
.App {
	background-color: $bg;
	color: white;
}

.container {

	.info {
		position: -webkit-sticky; /* for Safari */
		position: sticky;
		top: 0;
		align-self: flex-start; /* <-- this is the fix */
		padding: 10px;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		.sticky {
		}
	}
	.header {
		border-bottom: 1px solid darken($color: ($bg), $amount: 2);
		display: flex;
		align-items: center;
		background-color: $bg;
		position: fixed;
		z-index: 2;
		width: 100%;
		height: 40px;
		box-shadow: inset 0 5px darken($color: $bg, $amount: 5);
		div {
			padding: 10px;
			cursor: pointer;
			border: 1px solid darken($color: $bg, $amount: 2);
			&:hover {
				background-color: darken($color: $bg, $amount: 2);
			}
		}
	}
	
	.sections {
		padding-top: 40px;
	}
	@media (min-width: 992px) {
		.editor {
			border-left: 1px solid darken($color: $bg, $amount: 2);
		}
	}
}

